import React, { Component } from 'react';
import '../../styles/AmountHandler.css';
import humanIcon from '../../static/human.svg';

export default class AmountHandler extends Component {
  state = {
    counter: 2,
  }

  handleAddClick = () => {
    if (this.state.counter < 7) {
      this.setState(({ counter }) => ({
        counter: ++counter
      }))
    }
  }
  handleSubClick = () => {
    if (this.state.counter > 2) {
      this.setState(({ counter }) => ({
        counter: --counter,
      }))
    }
  }


  render() {
    const { counter } = this.state;
    return (
      <div className="amount-wrapper">
        <button className="sub-num" onClick={this.handleSubClick}>-</button>
          <input type="text" value={counter > 4 ? counter + " человек" : counter + " человека"} className="amount-input nav-input" readOnly />
        <button className="add-num" onClick={this.handleAddClick}>+</button>
      </div>
    )
  }
}
