import React, { Component } from 'react';
import Portal from '../Helpers/Portal';
import '../../styles/TimeHandler.css';


export default class TimeHandler extends Component {

  state = {
    active: false
  }

  createButtons = () => {
    let tables = []
    for (let i = 10; i <= 23; i++) {
      const timeBtn = React.createElement('button', { className: 'hoursBtn' }, i + ":00")
      tables.push(timeBtn)
      const timeBtn2 = React.createElement('button', { className: 'hoursBtn2' }, i + ":30")
      tables.push(timeBtn2)
    }
    return tables
  }

  toggleTimeBlock() {
    const currentState = this.state.active;
    this.setState({ active: !currentState })
  }

  windowClick = (event) => {
    const modal = document.querySelector('.modal');
    if (event.target === modal) {
      this.toggleTimeBlock();
    }
  }

  componentDidMount() {

    const timeHandler = document.getElementsByClassName('timeHandler')[0]
    const nowHours = new Date().getHours();
    if (nowHours > 10) {
      timeHandler.value = nowHours + 1 + ":00";
    }
    else {
      timeHandler.value = "10:00"
    }

    const timeBtn = document.getElementsByClassName('hoursBtn')
    const timeBtn2 = document.getElementsByClassName('hoursBtn2')

    for (let tb of timeBtn) {
      if (tb.innerHTML.substring(0, 2) <= nowHours) {
        tb.classList.add('hoursDisabled')
      }
      tb.addEventListener('click', function () {
        timeHandler.value = this.innerHTML;
      });
    }

    for (let tb of timeBtn2) {
      if (tb.innerHTML.substring(0, 2) <= nowHours) {
        tb.classList.add('hoursDisabled')
      }

      tb.addEventListener('click', function () {
        timeHandler.value = this.innerHTML;
      });
    }

    document.addEventListener('click', this.windowClick);
  }

  render() {
    return (
      <>
        <input type="text" className="timeHandler nav-input" onClick={() => this.toggleTimeBlock()} readOnly />
        <Portal>
          <div className={this.state.active ? 'modal show-modal' : "modal"}>
            <div className="timeBlock" onClick={() => this.toggleTimeBlock()}>
              {this.createButtons()}
            </div>
          </div>
        </Portal>
      </>
    )
  }
}
